html {
    font-size: 100%;
    box-sizing: border-box; //  ensures when you add padding total width is going to be included in padding so (width + padding)
}

*, *::before, *::after {
    box-sizing: inherit;
}

body {
    margin: 0;
    padding: 0;
    font-family: var(--font-inter);
    background: var(--dark-bg);
    color: var(--dark-text2)
}